import { Component } from 'react';

class Sections extends Component {
  constructor(props, opened = []) {
    super(props);
    this.state = {
      open: [...opened]
    };
    // handle #toggles
    this.handleHash(props);
  }

  componentWillReceiveProps(newProps) {
    this.handleHash(newProps);
  }

  handleHash = (props) => {
    const { hash } = props.router.location;
    if (hash !== '') {
      const name = hash.substring(1);
      const index = this.state.open.indexOf(name);
      if (index < 0) {
        this.state.open.push(name);
      }
    }
  }

  handleToggle = (name) => {
    return () => {
      const index = this.state.open.indexOf(name);
      if (index < 0) {
        this.state.open.push(name);
      } else {
        delete this.state.open[index];
      }
    };
  }
}

export default Sections;
